.popup__title {
  font-style: normal;
  font-weight: 900;
  font-size: 24px;
  line-height: 29px;
  color: #000000;
  margin: 0;
  padding: 34px 64px 0 36px;
}

@media screen and (max-width: 575.98px) {
  .popup__title {
    font-size: 18px;
    line-height: 22px;
    padding: 30px 35px 0 20px;
  }
}
